.side-drawer {
  height: 100%;
  background: white;
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;

  ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;


    button {
      background-image: linear-gradient(to right, #00A0F5, #6926C5, #6926C5);
    }
  }

  a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
  }
  a:hover,
  a:active {
    color: green;
  }
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}